*{
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
html, body{
    overflow: hidden;
    height: 100%;
    width: 100%;
}
canvas {
    background: -webkit-gradient(linear, left top, right top, from(black), color-stop(black), color-stop(black), color-stop(red), to(yellow));
    background: -webkit-linear-gradient(left, black, black, black, red, yellow);
    background: -o-linear-gradient(left, black, black, black, red, yellow);
    background: linear-gradient(to right, black, black, black, red, yellow);
}


